import React from "react";
import { Form, Input, Button, message, Switch } from "antd";
import { useForm } from "antd/es/form/Form";
import { User } from "../types";

interface RegisterUserProps {
  addUser: (user: Omit<User, "id">) => boolean;
  editingUser: User | null;
  onUpdateUser: (user: User) => boolean;
  setEditingUser: (user: User | null) => void;
}

const RegisterUser: React.FC<RegisterUserProps> = ({
  addUser,
  editingUser,
  onUpdateUser,
  setEditingUser,
}) => {
  const [form] = useForm();

  React.useEffect(() => {
    if (editingUser) {
      form.setFieldsValue(editingUser);
    }
  }, [editingUser, form]);

  const handleSubmit = async (values: Omit<User, "id">) => {
    try {
      let success;
      if (editingUser) {
        success = onUpdateUser({ ...values, id: editingUser.id });
        if (success) {
          message.success("   ");
          form.resetFields();
        }
      } else {
        success = addUser(values);
        if (success) {
          message.success("   ");
          form.resetFields();
        }
      }
    } catch (error) {
      message.error(" ");
    }
  };

  return (
    <div className="py-4 px-6 border rounded-lg shadow-lg bg-white w-full">
      <h2 className="text-xl font-bold mb-4">
        {editingUser ? " " : "  "}
      </h2>
      <Form form={form} layout="vertical" onFinish={handleSubmit}>
        <div className="grid grid-cols-1 xs:grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-0 sm:gap-2 lg:gap-4 -pb-4">
          <Form.Item
            label=" "
            name="userName"
            rules={[{ required: true, message: "   !" }]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            label=" "
            name="arabicUserName"
            rules={[
              { required: true, message: "   !" },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            label=" "
            name="englishUserName"
            rules={[
              { required: true, message: "   !" },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            label=" "
            name="password"
            rules={[{ required: true, message: "   !" }]}
          >
            <Input.Password />
          </Form.Item>
          <Form.Item
            label=" "
            name="isAdmin"
            valuePropName="checked"
            className=""
          >
            <Switch />
          </Form.Item>
        </div>
        <Form.Item className="">
          <div className="flex gap-2">
            <Button type="primary" htmlType="submit">
              {editingUser ? "" : ""}
            </Button>
            <Button
              onClick={() => {
                form.resetFields();
                setEditingUser(null);
              }}
            >
              
            </Button>
          </div>
        </Form.Item>
      </Form>
    </div>
  );
};

export default RegisterUser;
